<template>
  <div id="tabletRegister">
    <Button type="primary" size="small" @click="getWord" :loading="exportWordLoading" style="position: absolute;right: 58px;top:8px">导出</Button>
    <h2>生态保护红线界桩登记表</h2>
    <table border='0' cellpadding='0' cellspacing='0' id="tab">
      <tr>
        <td colspan='6' style="width: 25%">界桩编号</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.jzNumber}}</td>
        <td colspan='6' style="width: 25%">界桩刻号</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.jzKh}}</td>
      </tr>
      <tr>
        <td colspan='6'>所在地</td>
        <td colspan='18' style="font-weight: normal">{{data.placeName}}</td>
      </tr>
      <tr>
        <td colspan='6' rowspan='4'>界桩点与方位物的相关位置</td>
        <td colspan='6'>方向</td>
        <td colspan='6'>参照物</td>
        <td colspan='6'>距离（m）</td>
      </tr>
      <tr  v-for="(item,index) in lmMPositionsDate(data.lmMarkerRelationPositions)" :key="index">
        <td colspan='6' style="font-weight: normal">{{item.direction}}</td>
        <td colspan='6' style="font-weight: normal">{{item.ofReference}}</td>
        <td colspan='6' style="font-weight: normal">{{item.distance}}</td>
      </tr>
      <tr>
        <td colspan='6'>经度</td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(data.proofLon)}}</td>
        <td colspan='6'>纬度</td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(data.proofLat)}}</td>
      </tr>
      <tr>
        <td colspan='6'>界桩位置缩略图</td>
        <td colspan='6' id="image"><img :src="posUrl" alt="" v-if="posUrl !== null" @click="imgEnlarge()" style="width:100%"></td>
        <td colspan='6'>二维码</td>
        <td colspan='6'><img src="../../assets/circlesMark_images/QRcode.png" alt="" style="width:100%"></td>
      </tr>
      <tr>
        <td colspan='4' rowspan='2'>界桩现场照片</td>
        <td colspan='4'>东</td>
        <td colspan='4'>西</td>
        <td colspan='4'>南</td>
        <td colspan='4'>北</td>
        <td colspan='4'>中</td>
      </tr>
      <tr id="images">
        <td colspan='4' v-for="(item, index) in lmPhotos" :key="index">
          <div style="min-height: 120px;display: flex; justify-content: center;align-items: center;">
            <img :src="item.url" alt=""  @click="imgEnlarges()" v-if="item.url !== undefined " style="width:100%">
          </div>
        </td>
      </tr>
      <tr>
        <td colspan='6'>备注</td>
        <td colspan='18' style="font-weight: normal;text-align: left">{{data.remark}}</td>
      </tr>
      <tr>
        <td colspan='3'>填表人</td>
        <td colspan='3' style="font-weight: normal">{{data.createUserName}}</td>
        <td colspan='3'>采集日期</td>
        <td colspan='3' style="font-weight: normal">{{renderTime(data.createTime)}}</td>
        <td colspan='3'>审核人</td>
        <td colspan='3' style="font-weight: normal">{{data.verifyPerson}}</td>
        <td colspan='3'>审核日期</td>
        <td colspan='3' style="font-weight: normal">{{renderTime(data.verifyTime)}}</td>
      </tr>
    </table>

  </div>
</template>

<script>
import { getMarkerById, exportWord } from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import { cacuLonLat, renderTime } from '@/utils/common'
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'

export default {
  name: 'TabletRegister',
  data () {
    return {
      /* 获取的数据 */
      data: {
        lmMarkerRelationPositions: [],
        lmMarkerPhotos: []
      },
      viewer: {}, // viewerjs实例化对象,
      viewers: {}, // viewerjs实例化对象
      exportWordLoading: false
    }
  },
  computed: {
    /* 界桩方位物行高 */
    comLineHeight: function () {
      return this.data.lmMarkerRelationPositions.length === 0 ? (this.data.lmMarkerRelationPositions.length + 2) * 29 : (this.data.lmMarkerRelationPositions.length + 1) * 29
    },
    /* 获取位置缩略图路径 */
    posUrl: function () {
      let posurl = null
      for (let j in this.data.lmMarkerPhotos) {
        if (this.data.lmMarkerPhotos[j].type === 6) {
          posurl = this.data.lmMarkerPhotos[j].url
        }
      }
      if (posurl === null) {
        return posurl
      }
      return posurl
    },
    /* 处理界桩现场照片数据 */
    lmPhotos: function () {
      console.log(this.data.lmMarkerPhotos)
      let arr = [1, 3, 2, 4, 5]
      let photosData = [{}, {}, {}, {}, {}]
      for (let i in photosData) {
        for (let j in this.data.lmMarkerPhotos) {
          if (this.data.lmMarkerPhotos[j].type === arr[i]) {
            photosData[i] = this.data.lmMarkerPhotos[j]
          }
        }
        if (photosData[i] === '') {
          photosData[i].url = ''
        }
      }
      return photosData
    }
  },
  props: {
    tabletId: {}
  },
  watch: {
    tabletId () {
      this.getTableData()
    }
  },
  mounted () {
    this.viewer = new Viewer(document.getElementById('image'))
    this.viewers = new Viewer(document.getElementById('images'))
  },
  methods: {
    /* 获取原始界桩列表 */
    getTableData () {
      getMarkerById({
        accessToken: getToken(),
        validate: true,
        data: {
          id: this.tabletId
        }
      }).then((res) => {
        this.data = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 方位物数据处理 */
    lmMPositionsDate (lmMarkerRelationPositions) {
      if (lmMarkerRelationPositions.length < 3) {
        return lmMarkerRelationPositions.push({ direction: '-', ofReference: '-', distance: '-' })
      } else {
        return lmMarkerRelationPositions.slice(0, 3)
      }
    },
    /* 图片放大显示 */
    imgEnlarge () {
      this.viewer.update()
    },
    /* 图片放大显示 */
    imgEnlarges () {
      this.viewers.update()
    },
    /* 日期格式转换 */
    renderTime (date) {
      return renderTime(date)
    },
    /* 经纬度转换为度分秒 */
    cacuLonLat (e) {
      return cacuLonLat(e)
    },
    /* 导出Word文件 */
    getWord () {
      this.exportWordLoading = true
      exportWord({
        data: {
          id: this.tabletId
        }
      }).then((res) => {
        this.exportWordLoading = false
        if (res.data.code === '0000') {
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data
          link.href = url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.exportWordLoading = false
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  #tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
